<template>
    <section>
      <p class="section-caption">У вас уже есть свой сайт. Замечательно,
        половина пути пройдена! Но как сделать так,
        чтобы ваша целевая аудитория нашла<br>
        его и узнала о ваших товарах и услугах?
        Всё просто – заказать SEO-продвижение в компании WEB ALTERNATIVE.</p>
        <ul class="articles">
          <li class="articles-item"
              v-for="(article,i) in articles"
              :key="article.id"
              :style="article.bg"
              :index = i
              @mouseover="index = i"
              @mouseleave="index = null"
          >
            <a class="articles-item-link"
               :href="article.url"
            >{{article.topic}}</a>
            <transition name="fade"  tag="div">
              <div :key="article.id" class="articles-item-popup"
                   v-show="index == i"
              >
                <h3>Почему важна внутренняя оптимизация сайта?</h3>
                <div class="articles-item-popup__content">
                  <p>  Раскрутка любого проекта проходит в несколько этапов. Процедура внутренней оптимизации
                    призвана сделать ваш сайт лучше, увеличить шансы на вывод в топ поисковой выдачи.</p>
                  <p>Это предварительный этап работ, благодаря которому появится прочный фундамент для
                    выстраивания бизнеса в интернете. Наши специалисты знают всё об эффективной оптимизации сайтов.
                    Мы проанализируем имеющиеся тексты и изображения, навигацию по сайту, тщательно проверим код на
                    наличие серьезных ошибок, протестируем юзабилити. Проведение предварительных работ позволит сделать
                    ваш сайт более заметным в интернете, увеличить его позиции в поисковой выдаче по интересующим
                    вас поисковым запросам.</p>
                </div>
                <a href="#!" class="btn-more">Узнать подробнее</a>
              </div>
            </transition>
          </li>
        </ul>
      <Advantages/>
      <section-footer></section-footer>
    </section>
</template>
<script>
  import {mapState} from 'Vuex'
  import Advantages from './Advantages'
  import SectionFooter from './SectionFooter'
  export default {
    components: {
      Advantages,
      SectionFooter
    },
    data() {
      return {
        show: false,
        index: null
      }
    },
    computed: {
      ...mapState(['articles']),
    },
  }
</script>
<style lang="scss" scoped>
  section {
    padding-top: 40px;
  }
  .section-caption {
    padding: 0 20px;
    text-align: justify;
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 40px;
  }
  .articles {
    display: flex;
    flex-wrap: wrap;
    padding: 40px 10px;
    margin-bottom: 46px;
    background: url('/static/img/bg_section1.jpg');
    &-item {
      position: relative;
      width: 100%;
      height: 248px;
      padding: 13px;
      background-size: cover !important;
      &:not(:last-child) {
        margin-bottom: 70px;
      }
      &-link {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        border: 1px solid rgba(#fff,.5);
        color: #fff;
        height: 100%;
        font-size: 20px;
      }
      &-popup {
        position: absolute;
        top: 50%;
        left: 0;
        z-index: 10;
        display: flex;
        flex-direction: column;
        align-items: center;
        max-height: 130%;
        overflow: hidden;
        transform: translateY(-50%);
        background-color: #108be9;
        padding: 30px 28px;
        color: #ffffff;
        box-shadow: 0 0 8px 4px rgba(0,0,0,.3);
        h3 {
          text-align: center;
          font-size: 22px;
          font-weight: 400;
          margin-bottom: 16px;
          padding: 0 40px;
        }
        &__content {
          margin-bottom: 31px;
          max-height: 55px;
          p {
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            overflow: hidden;
            font-size: 16px;
            font-weight: 300;
            &:last-of-type {
              display: none;
            }
          }
        }
      }
    }
  }
  @media only screen  and (min-width: 640px) and (max-width: 1024px) {
    .articles {
      justify-content: space-between;
      padding-bottom: 0;
      &-item {
        &:not(:last-child) {
          margin-bottom: 10px;
        }
        width: 49%;
      }
    }
  }
  @media only screen  and (min-width: 1024px) and (max-width: 1199px) {
    .articles {
      flex-wrap: nowrap;
      background: none;
      &-item {
        &:not(:last-child) {
          margin-bottom: 0;
        }
        &-popup {
          h3 {
            padding: 0;
            font-size: 20px;
          }
        }
      }
    }
    .btn-more {
      font-size: 16px;
    }
  }
  @media only screen and (min-width: 1200px) {
    .container {
      max-width: 1180px;
    }
    section {
      padding-top: 99px;
    }
    .section-caption {
      max-width: 1140px;
      font-size: 18px;
      margin: 0 auto 57px;
      text-align: center;
    }
    .articles {
      flex-wrap: nowrap;
      background: none;
      padding: 0;
      &-item {
        position: relative;
        &:not(:last-child) {
          margin-bottom: 0;
        }
        &-link {
          display: block;
          padding: 124px 40px 20px;
        }
        &-popup {
          /*max-height: 528px;*/
          max-height: initial;
          min-height: 528px;
          h3 {
            padding: 0 40px;
          }
          &__content {
            margin-bottom: 31px;
            max-height: 100%;
            p {
              display: block;
              margin-bottom: 20px;
              &:last-of-type {
                display: block;
                margin-bottom: 0;
              }
            }
          }
        }
      }
    }
    .fade-enter-action, .fade-leave-action {
      transition: opacity .5s ease-in-out;
    }
    .fade-enter, .fade-leave {
      opacity: 0;
    }
    .fade-enter-to, .fade-leave-to {
      opacity: 1;
    }
  }
</style>



